@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
.start-page{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;margin:0 auto}@media only screen and (min-width: 320px){.start-page{width:300px}}@media only screen and (min-width: 375px){.start-page{width:355px}}@media only screen and (min-width: 425px){.start-page{width:405px}}@media only screen and (min-width: 768px){.start-page{width:500px}}.start-page__title{display:block;font-weight:400;text-align:center;color:var(--color-white)}@media only screen and (min-width: 320px){.start-page__title{font-size:20px;line-height:26px}}@media only screen and (min-width: 375px){.start-page__title{font-size:22px}}@media only screen and (min-width: 425px){.start-page__title{font-size:24px;line-height:30px}}@media only screen and (min-width: 768px){.start-page__title{font-size:40px;line-height:46px}}.start-page__info{display:block;font-weight:300;color:var(--color-white)}@media only screen and (min-width: 320px){.start-page__info{margin-top:10px;font-size:18px;line-height:26px;text-align:center}}@media only screen and (min-width: 375px){.start-page__info{margin-top:15px;font-size:20px}}@media only screen and (min-width: 425px){.start-page__info{margin-top:20px;font-size:22px;line-height:28px}}@media only screen and (min-width: 768px){.start-page__info{margin-top:25px;font-size:28px;line-height:36px}}.start-page__buttons{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%}@media only screen and (min-width: 320px){.start-page__buttons{margin-top:15px}}@media only screen and (min-width: 375px){.start-page__buttons{margin-top:20px}}@media only screen and (min-width: 425px){.start-page__buttons{margin-top:30px}}@media only screen and (min-width: 768px){.start-page__buttons{margin-top:50px}}.start-page__button{display:flex;justify-content:center;flex-grow:1;font-weight:400;color:var(--color-white);border:2px solid var(--color-white);background-color:transparent;transition:all 0.5s ease;background-image:none}@media only screen and (min-width: 320px){.start-page__button{margin-right:5px;padding:15px 0;font-size:18px;line-height:24px}}@media only screen and (min-width: 375px){.start-page__button{font-size:20px}}@media only screen and (min-width: 425px){.start-page__button{font-size:22px}}@media only screen and (min-width: 768px){.start-page__button{margin-right:25px;padding:15px 0;line-height:28px}}.start-page__button:hover{background-color:var(--color-hover);transition:all 0.5s ease;cursor:pointer;border-radius:50px}.start-page__button:last-child{margin-right:0}

.back-button{position:absolute;display:flex;justify-content:center;align-items:center;font-size:50px;line-height:50px;font-weight:400;color:var(--color-white);border-radius:50%}@media only screen and (min-width: 320px){.back-button{top:5px;left:5px;width:35px;height:35px}}@media only screen and (min-width: 375px){.back-button{width:40px;height:40px}}@media only screen and (min-width: 425px){.back-button{top:10px;left:10px;width:40px;height:40px}}@media only screen and (min-width: 768px){.back-button{top:15px;left:15px;width:50px;height:50px}}.back-button svg{fill:var(--color-white)}

.normalizer{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh}.normalizer__header{display:flex}.normalizer__header-text{display:block;font-size:22px;line-height:26px;font-weight:300;text-align:justify;color:var(--color-white)}.normalizer__text{display:block;margin-top:100px;font-size:32px;line-height:40px;font-weight:400;text-align:justify;color:var(--color-white)}.normalizer__bars{display:flex;flex-direction:column;align-items:center;margin-top:50px}.normalizer__bar{display:flex;height:15px;margin-bottom:25px;background-color:var(--color-white);border-radius:50px;opacity:0;transition:1s ease}.normalizer__bar--1{width:200px}.normalizer__bar--2{width:175px}.normalizer__bar--3{width:150px}.normalizer__bar--4{width:125px}.normalizer__bar--5{width:100px}.normalizer__bar:last-child{margin-bottom:0}.normalizer__bar--show{opacity:1;transition:1s ease}.normalizer__timer{display:flex;margin-top:50px}.normalizer__timer-text{display:block;font-size:28px;line-height:36px;font-weight:400;text-align:justify;color:var(--color-white)}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

:root{--color-primary-1: #2a94bf;--color-primary-2: #a1a1e4;--color-black: #000000;--color-white: #ffffff;--color-hover: rgba(255, 255, 255, 0.3)}html{margin:0;padding:0;box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;padding:0;font-family:'Montserrat', -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Segoe UI', 'Roboto', 'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-style:normal;background:linear-gradient(to top, #2a94bf, #a1a1e4);background:linear-gradient(to top, var(--color-primary-1), var(--color-primary-2))}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none}img{display:block;max-width:100%;height:auto}

